import React, {useState} from 'react'
import { makeStyles } from '@material-ui/core/styles'
import TextField from '@material-ui/core/TextField'
import Container from '@material-ui/core/Container'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import SnackBar from '@material-ui/core/Snackbar'
import * as emailjs from 'emailjs-com'
import ContactMailIcon from '@material-ui/icons/ContactMail';


const useStyles = makeStyles(theme => ({
    container: {
        height: '90vh',
        background: theme.mode.bgColor,
        color: theme.mode.textColor,
        [theme.breakpoints.down('sm')]: {
            height: "800px"
        }
    },
    root: {
        [theme.breakpoints.down('sm')]: {
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
            marginBottom: '10rem'
        }
    },
    formArea: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'space-around',
        height: "100%"
    },
    gridItem: {
        height: '500px',
        width: '60%'
    },
    socialLinks: {
        display: "flex",
        flexDirection: "column",
        height: '100%'
    },
    links: {
        flexBasis: '50%',
        display: "flex",
        justifyContent: 'space-around',
        flexDirection: 'column'
    },
    or: {
        display: "flex",
        justifyContent: 'center',
        alignItems: 'center',
        height: '100%',
        fontWeight: "bolder"
    },
    close: {
        padding: theme.spacing(0.5)
    },
    sendButton: {
        padding: '10px',
        background: '#fff',
        color: '#0984e3',
        border: '1px solid #0984e3',
        borderRadius: '5px',
        transition: '0.25s all',
        '&:hover': {
            background: '#f2f4f6'
        },
        cursor: 'pointer'
    },
    resume: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        padding: '10px',
        background: theme.mode.bgColor,
        color: theme.mode.textColor
    },
    inputField: {
        background: theme.mode.bgColor
    },
}))

const socialLinks = [
    {
        title: "Reddit",
        link: "https://www.reddit.com/user/tech008",
        styles: {
            color: "#ff4500",
            border: "1px solid #ff4500",
            padding: '5px 10px',
            cursor: 'pointer',
            borderRadius: '10px',
            textAlign: 'center'
        }
    },
    {
        title: "Discord",
        link: "#",
        styles: {
            color: "#7289da",
            border: "1px solid #7289da",
            padding: '5px 10px',
            cursor: 'pointer',
            borderRadius: '10px',
            textAlign: 'center'
        }
    },
    {
        title: "Twitter",
        link: "https://twitter.com/The1WhoIsMany",
        styles: {
            color: "#1da1f2",
            border: "1px solid #1da1f2",
            padding: '5px 10px',
            cursor: 'pointer',
            borderRadius: '10px',
            textAlign: 'center'
        }
    }
]

export default function ContactSection() {
    const classes = useStyles()
    const [isSent, setSent] = useState(false)
    const [email, setEmail] = useState('')
    const [name, setName] = useState('')
    const [message, setMessage] = useState('')
    const [response, setResponse] = useState(null)

    const handleName = e => {
        e.preventDefault()
        setName(e.target.value)
    }

    const handleEmail = e => {
        e.preventDefault()
        setEmail(e.target.value)
    }

    const handleMessage = e => {
        e.preventDefault()
        setMessage(e.target.value)
    }

    const handleSubmit = (event) => {
        event.preventDefault()
        console.log(email, name, message)
        console.log("Hello")
        emailjs.send('outlook', 'contactme', {email: email, name: name, content: message}, "user_KAlPGKueC7xXx9KGLXcJC")
        .then(response => {
            setSent(true)
            setResponse("Mail sent successfully!")
            setMessage('')
            setName('')
            setEmail('')
            setTimeout(() => setSent(false), 3000)
        })
        .catch(error => {
            setSent(true)
            setResponse("An error occured! Please try again")
            setTimeout(() => setSent(false), 3000)
        })
    }

    return (
        <Container maxWidth = "lg" className = {classes.container}>
            <h1><ContactMailIcon /> Contact me!</h1>
            <Grid container className = {classes.root} spacing = {3}>
                <Grid item xs = {12} md = {5} className = {classes.gridItem}>
                    <form className = {classes.formArea} onSubmit = {handleSubmit}>
                        <h3>Send me a mail!</h3>
                        <TextField id = "email" InputProps = {{className: {color: '#fff'}}} label = "Email" variant = "outlined" value = {email} onChange = {handleEmail} className = {classes.inputField} />
                        <TextField id = "name" InputProps = {{className: {color: '#fff'}}} label = "Name" variant = "outlined" value = {name} onChange = {handleName} className = {classes.inputField} />
                        <TextField 
                            id = "message"
                            label = "Message"
                            rows = "4"
                            multiline
                            variant = "outlined"
                            value = {message}
                            onChange = {handleMessage}
                            className = {classes.inputField}
                            InputProps = {{className: {color: '#fff'}}}
                        />
                        <button type = "submit" className = {classes.sendButton}>Send mail!</button>
                        <SnackBar
                            anchorOrigin = {{
                                vertical: 'bottom',
                                horizontal: 'left'
                            }}
                            open = {isSent}
                            autoHideDuration = {5000}
                            message = {<span id = "message">{response}</span>}
                        />
                    </form>
                </Grid>
                <Grid item xs = {12} md = {2} className = {classes.gridItem}>
                    <Typography component = "div" className = {classes.or}>
                        Or
                    </Typography>
                </Grid>
                <Grid item xs = {12} md = {5} className = {classes.gridItem}>
                    <Typography component = "div" className = {classes.socialLinks}>
                        <h3 style = {{flexBasis: '5%'}}>Connect with me on social media!</h3>
                        <Typography component = "div" className = {classes.links}>
                            {socialLinks.map((social, i) => 
                                <Typography key = {i} component = "div" style = {social.styles} onClick = {() => window.open(social.link)}>
                                    {social.title}
                                </Typography>)}
                        </Typography>
                    </Typography>
                </Grid>
                <Grid item xs = {12} className = {classes.resume}>
                    <Typography component = "div">
                        Found my profile interesting? You can check my full resume <a href = "https://www.dropbox.com/s/esel481omvybl2z/Resume.pdf?dl=0" rel="noopener noreferrer" target = "_blank">here</a>
                    </Typography>
                </Grid>
            </Grid>
        </Container>
    )
}