import React from 'react'
import Card from '@material-ui/core/Card'
import CardMedia from '@material-ui/core/CardMedia'
import CardContent from '@material-ui/core/CardContent'
import Container from '@material-ui/core/Container'
import Grid from '@material-ui/core/Grid'
import { makeStyles } from '@material-ui/core/styles'
import grey from '@material-ui/core/colors/grey'

const useStyles = makeStyles(theme => ({
    container: {
        background: theme.mode.bgColor,
        color: theme.mode.textColor,
    },
    card: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        background: theme.mode.bgColor,
        color: theme.mode.textColor,
        alignItems: 'center',
        height: '90vh',
        [theme.breakpoints.up('md')]: {
            margin: '0 4rem',
        },
        [theme.breakpoints.down('md')]: {
            margin: '0 1rem'
        }
    },
    name: {
        fontWeight: 600,
        fontSize: '1.5rem'
    },
    company: {
        fontWeight: 400,
        fontSize: '0.8rem',
        color: grey[500]
    },
    image: {
        borderRadius: '50%',
        width: '200px',
        height: '200px',
        [theme.breakpoints.down('md')]: {
            width: '150px',
            height: '150px'
        }
    }
}))

const AboutSection = props => {
    const classes = useStyles()
    return (
        <Container maxWidth = "lg" className = {classes.container}>
            <Card className = {classes.card}>
                <CardMedia image = {require('../images/abhishek-prashant.jpg')} title = "Profile picture" className = {classes.image} />
                <CardContent>
                    <Grid container spacing = {2} direction = "column" justify = "center" alignItems = "center">
                        <Grid item xs = {12}>
                            <Grid container spacing = {1} direction = "column" justiy = "center" alignItems = "center">
                                <Grid item xs = {12} className = {classes.name}>Abhishek Prashant</Grid>
                                <Grid item xs = {12} className = {classes.company}>Javascript Developer @ Prosavinns Pvt Ltd (Oct 2019 - Present)</Grid>
                            </Grid>
                        </Grid>
                        <Grid item xs = {12}>
                            I am a software developer specializing in Python and Javascript. I am currently working at a startup in Nagpur where I manage their website's JS and
                            and backend which is written in flask. I have recently been fascinated by React-Native and Mobile App development in general and I am learning that
                            in my spare time.
                        </Grid>
                    </Grid>
                </CardContent>
            </Card>
        </Container>
    )
}

export default AboutSection