import React from 'react'
import TreeView from '@material-ui/lab/TreeView'
import Rating from '@material-ui/lab/Rating'
import Container from '@material-ui/core/Container'
import { makeStyles } from '@material-ui/core/styles'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import TreeItem from '@material-ui/lab/TreeItem'
import Grid from '@material-ui/core/Grid';
import lightGreen from '@material-ui/core/colors/lightGreen'
import StarsIcon from '@material-ui/icons/Stars';
import CenterElements from './centeralign'
import { useTheme } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery'
import Tooltip from '@material-ui/core/Tooltip'


const useStyles = makeStyles(theme => ({
    container: {
        height: '90vh',
        marginTop: '5rem',
        color: theme.mode.textColor
    },
    header: {
        flex: '1 1'
    },
    langmain: {
        display: "flex",
        justifyContent: 'space-around',
        [theme.breakpoints.down('md')]: {
            flexDirection: 'column'
        }
    },
    tree: {
        display: "flex",
        flexWrap: "wrap"
    },
    treeNode: {
        color: lightGreen[500],
        padding: '8px 13px',
    },
    softskills: {
        display: "flex",
        justifyContent: "space-around",
        flexWrap: "wrap",
        alignItems: "space-between",
        maxHeight: "130px",
        [theme.breakpoints.down('md')]: {
            marginTop: '4rem',
            flexWrap: "wrap"
        }
    }
}))

const LanguageSection = () => {
    const classes = useStyles()
    const theme = useTheme()
    const isMobile = useMediaQuery(theme.breakpoints.down('md'))
    const resolution = isMobile? "64": "128"
    const softSkillsData = [
        {
            title: "Adobe Photoshop",
            base_url: require(`../images/photoshop-${resolution}.png`)
        },
        {
            title: "Figma",
            base_url: require(`../images/figma-${resolution}.png`)
        },
        {
            title: "Github",
            base_url: require(`../images/github-${resolution}.png`)
        },
        {
            title: "Microsoft Excel",
            base_url: require(`../images/excel-${resolution}.png`)
        },
        {
            title: "Adobe XD",
            base_url: require(`../images/adobe-xd-${resolution}.png`)
        },
        {
            title: "Jupyter Notebook",
            base_url: require(`../images/jupyter-${resolution}.png`)
        }
    ]
    
    return (
        <Container maxWidth = 'lg' className = {classes.container}>
            <div className = {classes.header}>
                <h1><StarsIcon /> Technical and soft skills</h1>
            </div>
            <Grid xs = {12} className = {classes.langmain}>
                <Grid item xs = {12} md = {6} justify = "center">
                    <TreeView
                        defaultCollapseIcon = {<ExpandMoreIcon />}
                        defaultExpandIcon = {<ChevronRightIcon />}
                    >
                        <TreeItem nodeId = "python" label = {<CenterElements div1 = {<div>Python</div>} div2 = {<Rating name = "read-only" value = {4} readOnly />} />} className = {classes.treeNode}>
                            <TreeItem nodeId = "flaskdjango" label = "Flask and Django" className = {classes.treeNode} />
                            <TreeItem nodeId = "datastructures" label = "Data structures and Algorithms" className = {classes.treeNode} />
                            <TreeItem nodeId = "datascience" label = "Pandas, Matplotlib and Numpy" className = {classes.treeNode} />
                            <TreeItem nodeId = "datamining" label = "Selenium, scrapy and BeautifulSoup4" className = {classes.treeNode} />
                        </TreeItem>
                        <TreeItem nodeId = "javascript" label = {<CenterElements div1 = {<div>Javascript</div>} div2 = {<Rating name = "read-only" value = {4} readOnly />} />} className = {classes.treeNode}>
                            <TreeItem nodeId = "react" label = "React.js" className = {classes.treeNode} />
                            <TreeItem nodeId = "reactnative" label = "React Native" className = {classes.treeNode} />
                            <TreeItem nodeId = "DOM" label = "Vanilla JS, Virtual DOM" className = {classes.treeNode} />
                            <TreeItem nodeId = "jquery" label = "jQuery" className = {classes.treeNode} />
                        </TreeItem>
                        <TreeItem nodeId = "HTML5CSS3" label = {<CenterElements div1 = {<div>HTML5, CSS3</div>} div2 = {<Rating name = "read-only" value = {3} readOnly />} />} className = {classes.treeNode}>
                            <TreeItem nodeId = "flexbox" label = "CSS Flexbox" className = {classes.treeNode} />
                            <TreeItem nodeId = "grid" label = "CSS Grid" className = {classes.treeNode} />
                            <TreeItem nodeId = "responsive" label = "Responsive Layouts using media query breakpoints" className = {classes.treeNode} />
                        </TreeItem>
                    </TreeView>
                </Grid>
                <Grid item xs = {12} md = {6} className = {classes.softskills}>
                    {/* <img src = {require(`../images/figma-${resolution}.png`)} alt = "Figma" />
                    <img src = {require(`../images/photoshop-${resolution}.png`)} alt = "Adobe Photoshop" />
                    <img src = {require(`../images/github-${resolution}.png`)} alt = "Github" />
                    <img src = {require(`../images/excel-${resolution}.png`)}  alt  = "Excel" />
                    <img src = {require(`../images/adobe-xd-${resolution}.png`)} alt = "Adobe XD" />
                    <img src = {require(`../images/jupyter-${resolution}.png`)} alt = "Jupyter Notebook" /> */}
                    {softSkillsData.map((skill, i) => 
                        <Tooltip key = {i} title = {skill.title}>
                            <img src = {skill.base_url} alt = {skill.title} />
                        </Tooltip>)}
                </Grid>
            </Grid>
        </Container>
    )
}

export default LanguageSection