import React, { useState } from 'react'
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import ContactlessIcon from '@material-ui/icons/Contactless';
import GitHubIcon from '@material-ui/icons/GitHub';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import Grid from '@material-ui/core/Grid';
import FavoriteIcon from '@material-ui/icons/Favorite';
import TwitterIcon from '@material-ui/icons/Twitter';
import InstagramIcon from '@material-ui/icons/Instagram';
import RedditIcon from '@material-ui/icons/Reddit';
import { makeStyles } from '@material-ui/core/styles'
import lightGreen from '@material-ui/core/colors/lightGreen'
import grey from '@material-ui/core/colors/grey'
import Link from '@material-ui/core/Link'
import MenuIcon from '@material-ui/icons/Menu';
import useMediaQuery from '@material-ui/core/useMediaQuery'
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import { useTheme } from '@material-ui/core/styles';
import ListItemIcon from '@material-ui/core/ListItemIcon'
import pink from '@material-ui/core/colors/pink'
import CenterElements from './centeralign'
import Brightness7Icon from '@material-ui/icons/Brightness7';
import Brightness2Icon from '@material-ui/icons/Brightness2';


const useStyles = makeStyles(theme => ({
    Body: {
        background: theme.mode.bgColor
    },
    ToolLink: {
        margin: '0 10px',
        color: grey[50],
        textDecoration: 'none',
        transition: '0.25s all',
        '&:hover': {
            color: pink[400],
        },
        cursor: 'pointer'
    },
    ThemeColor: {
        backgroundColor: lightGreen[500],
    },
    list: {
        width: 250,
    },
    footer: {
        position: 'relative',
        bottom: 0,
    }
}))

const linksData = [
    {
        name: "Github",
        link: "https://github.com/t3chcrazy",
        icon: <GitHubIcon />
    },
    {
        name: "Linkedin",
        link: "https://www.linkedin.com/in/abhishek-prashant-92b152a9/",
        icon: <LinkedInIcon />
    },
    {
        name: "Blog",
        link: "https://abhishekblog-1847184483.gtsb.io/",
        icon: <ContactlessIcon />
    }
]

const Layout = props => {
    const theme = useTheme()
    const isMobile = useMediaQuery(theme.breakpoints.down('md'))
    const [state, setState] = useState({'right': false})
    const classes = useStyles()
    const toggleDrawer = (side, open) => event => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }
        setState({...state, [side]: open})
    }

    const toggleMode = event => {
        props.setMode(!props.isDarkMode)
    }

    const hambergerMenu = side => (
        <div className = {classes.list} role = "presentation" onClick = {toggleDrawer(side, false)} onKeyDown = {toggleDrawer(side, false)}>
            <List>
                {linksData
                .map((el, i) => 
                    <Link key = {i} href = {el.link}>
                        <ListItem button key = {el.name}>
                            <ListItemIcon>{el.icon}</ListItemIcon>
                            <ListItemText primary = {el.name} />
                        </ListItem>
                    </Link>)}
            </List>
        </div>
    )
    return (
        <div className = {classes.Body}>
            <AppBar position = "static" className = {classes.ThemeColor}>
                <Toolbar>
                    <h3 style = {{flexGrow: 1}}>My Website</h3>
                    {isMobile? 
                    <>
                        <div className = {classes.ToolLink} onClick = {toggleMode}>
                            {props.isDarkMode? <Brightness2Icon /> :<Brightness7Icon />}
                        </div>
                        <MenuIcon onClick = {toggleDrawer('right', true)} style = {{cursor: `pointer`}} />
                        <Drawer anchor = "right" open = {state.right} onClose = {toggleDrawer('right', false)}>
                            {hambergerMenu('right')}
                        </Drawer>
                    </>: 
                    <>
                        <div className = {classes.ToolLink} onClick = {toggleMode}>
                            {props.isDarkMode? <Brightness7Icon />: <Brightness2Icon />}
                        </div>
                        {linksData.map(el => 
                            <Link key = {el.name} href = {el.link} className = {classes.ToolLink}>
                                <CenterElements div1 = {el.icon} div2 = {<div>{el.name}</div>} />
                            </Link>)}
                    </>}
                </Toolbar>
            </AppBar>
            {props.children}
            <div className = {classes.footer}>
                <Grid container spacing = {2} direction = "column" justify = "center" alignItems = "center" className = {classes.ThemeColor}>
                    <Grid item xs = {12} style = {{display: "flex"}}><CenterElements div1 = {<CenterElements div1 = {<div>Made with</div>} div2 = {<FavoriteIcon />} />} div2 = {<div>by Abhishek Prashant</div>} /></Grid>
                    <Grid item xs = {12}>Follow me on social media 
                        <Link href = "https://twitter.com/The1WhoIsMany" className = {classes.ToolLink}><TwitterIcon /></Link>
                        <Link href = "https://www.reddit.com/user/tech008" className = {classes.ToolLink}><RedditIcon /></Link>
                        <Link href = "https://www.instagram.com/tech_crazy/" className = {classes.ToolLink}><InstagramIcon /></Link>
                    </Grid>
                </Grid>
            </div>
        </div>
    )
}

export default Layout