import React, {useState} from 'react'
import { makeStyles } from '@material-ui/core/styles'
import ExpansionPanel from '@material-ui/core/ExpansionPanel'
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary'
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import Container from '@material-ui/core/Container'
import Chip from '@material-ui/core/Chip'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import { useTheme } from '@material-ui/core/styles'
import useMediaQuery from '@material-ui/core/useMediaQuery'
import EmojiEventsIcon from '@material-ui/icons/EmojiEvents'
import lightGreen from '@material-ui/core/colors/lightGreen'


const useStyles = makeStyles(theme => ({
    container: {
        height: '90vh',
        marginTop: '5rem',
        color: theme.mode.textColor
    },
    header: {
        flexBasis: '33.33%',
        flexShrink: 0,
        fontWeight: "bolder",
        color: lightGreen[500],
        [theme.breakpoints.down('md')]: {
            flexBasis: '50%'
        }
    },
    chipHeader: {
        display: 'flex',
        '& > *': {
            margin: '0 5px'
        },
        [theme.breakpoints.down('md')]: {
            fontSize: '10px',
            display: 'inline-block',
        },
    },
    panelSummary: {
        background: theme.mode.bgColor,
        color: theme.mode.textColor
    },
    panelDescription: {
        background: theme.mode.bgColor,
        color: theme.mode.textColor
    }
}))

const projects = [
    {
        title: "A discord bot",
        chips: [
            "Python",
            "REST API",
            "Discord"
        ],
        descriptions: [
            "It is a chat bot to provide various functionality and improved user experience.",
            "A discord bot designed using Pythons Discord API which acquires data using Hirez Studios REST API. The bot prepares a URL based on the command entered in discord chat, encodes it and gets JSON data from specified endpoint."
        ],
        link: "https://github.com/ashu2345/PaladinsStats)"
    },
    {
        title: "Online Restaurant order and payment service",
        chips: [
            "Python",
            "Django",
            "Bootstrap",
            "Django user auth"
        ],
        descriptions: [
            "A Django web application for managing a restaurants available inventory, with front-end templates developed with HTML and enhanced with Bootstrap.",
            "A login and registration form extending Djangos user model including features like adding items to their cart, checking their order history, integrated authorize.net to simulate a banks authorization process, etc."
        ],
        link: "https://github.com/Tech-Crazy/django-project"
    },
    {
        title: "Website Scraper + Data analysis",
        chips: [
            "Python",
            "Scrapy",
            "Pandas",
            "Jupyter notebook"
        ],
        descriptions: [
            "A website scraper designed using Pythons Scrapy module which scrapes through a celebrity website gathering data for athletes and saves them in csv format which is ready for data analysis.",
            "The data analysis is performed using Pandas and Numpy and visualization is rendered using Bokeh.",
            <a href = "https://colab.research.google.com/drive/1BuAYvgg0XMs7HZjYekFa8tqdwSex0Z1k" target = "_blank" rel="noopener noreferrer">Google colab</a>
        ],
        link: "https://github.com/Tech-Crazy/Website-Scraper"
    }
]

export default function ProjectSection() {
    const [expanded, setExpanded] = useState(false)

    const theme = useTheme()
    const isMobile = useMediaQuery(theme.breakpoints.down('md'))

    const handleChange = panel => (event, isExpanded) => {
        setExpanded(isExpanded? panel: false)
    }

    const ResponsiveChip = props => {
        if (isMobile) {
            return <Chip size = "small" {...props} />
        }
        return <Chip {...props} />
    }

    const classes = useStyles()

    return (
        <Container maxWidth = "lg" className = {classes.container}>
            <h1><EmojiEventsIcon /> My projects</h1>
            {projects.map((project, i) => 
                <>
                    <ExpansionPanel key = {i} expanded = {expanded === `panel${i}`} onChange = {handleChange(`panel${i}`)}>
                        <ExpansionPanelSummary
                        expandIcon = {<ExpandMoreIcon />}
                        id = {`panel${i}`}
                        className = {classes.panelSummary}>
                            <div className = {classes.header}>
                                {project.title}
                            </div>
                            <div className = {classes.chipHeader}>
                                {project.chips.map((chip, i) => <ResponsiveChip key = {i} label = {chip} />)}
                            </div>
                        </ExpansionPanelSummary>
                        <ExpansionPanelDetails className = {classes.panelDescription}>
                            <List component = "nav">
                                {project.descriptions.map((desc, i) => 
                                    <ListItem key = {i}>
                                        <ListItemText primary = {desc} />
                                    </ListItem>)}
                                <ListItem>
                                    <ListItemText>
                                        <a href = {project.link} target = "_blank" rel="noopener noreferrer">Project Link</a>
                                    </ListItemText>
                                </ListItem>
                            </List>
                        </ExpansionPanelDetails>
                    </ExpansionPanel>
                </>)}
        </Container>
    )
}