import React, {useState} from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import Container from '@material-ui/core/Container'
import Box from '@material-ui/core/Box'
import Typography from '@material-ui/core/Typography'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import lightGreen from '@material-ui/core/colors/lightGreen'
import VerifiedUserIcon from '@material-ui/icons/VerifiedUser';


const useStyles = makeStyles(theme => ({
    container: {
        height: '90vh',
        display: 'flex',
        justifyContent: 'center',
    },
    tabsRoot: {
        height: 200,
        display: 'flex',
        backgroundColor: theme.palette.background.paper,
        maxWidth: '800px',
        marginLeft: '-20%',
        [theme.breakpoints.down('md')]: {
            marginLeft: 0
        },
        background: theme.mode.bgColor,
        color: theme.mode.textColor
    },
    tabs: {
        borderRight: `1px solid ${theme.palette.divider}`,
        background: theme.mode.bgColor,
        color: theme.mode.textColor
    },
    tabPanel: {
        background: theme.mode.bgColor,
        color: theme.mode.textColor,
        maxWidth: '300px', 
        minWidth: '300px'
    }
}))

const certificationSource = ["NPTEL", "Vskills", "MongoDB University"]

const certificationCourses = [
    [
        "Data Science For Engineers",
        "Data structures and Algorithms using Python"
    ],
    [
        "Certified Python Developer"
    ],
    [
        "M001 MongoDB Basics",
        "M103 MongoDB for Python Developers"
    ]
]

const tabProps = index => {
    return {
        id: `vertical-tab-${index}`,
        'aria-controls': `vertical-tabpanel-${index}`
    }
}

export default function CertificationSection() {
    const [value, setValue] = useState(0)
    const classes = useStyles()

    const TabPanel = props => {
        const { children, value, index } = props
        return (
            <Typography
                component = "div"
                role = "tabpanel"
                hidden = {value !== index}
                id = {`vertical-tabpanel-${index}`}
                aria-labelledby = {`vertical-tab-${index}`}
                className = {classes.tabPanel}
            >
                <Box p = {1}>
                    {children}
                </Box>
            </Typography>
        )
    }

    const handleChange = (event, i) => {
        setValue(i)
    }
    return (
        <Container maxWidth = "lg">
            <h1>
                <VerifiedUserIcon /> Certifications
            </h1>
            <Container maxWidth = "lg" className = {classes.container}>
                <div className = {classes.tabsRoot}>
                    <Tabs
                        orientation = "vertical"
                        variant = "scrollable"
                        value = {value}
                        onChange = {handleChange}
                        aria-label = "Vertical tabs"
                        className = {classes.tabs}
                    >
                        {certificationSource.map((cert, i) => <Tab style = {{color: lightGreen[500]}} key = {i} label = {cert} {...tabProps(i)} />)}
                    </Tabs>
                    {certificationCourses.map((courses, i) => 
                        <TabPanel key = {i} value = {value} index = {i}>
                            <List>
                                {courses.map((course, i) => 
                                <ListItem>
                                    <ListItemText key = {i} primary = {course}/>
                                </ListItem>)
                            }
                            </List>
                        </TabPanel>)}
                </div>
            </Container>
        </Container>
    )
}